import React, { Component, useEffect, useState } from 'react';
import { Switch, Route } from 'react-router-dom';
import {messaging, showNotification} from './Global/Variables';

//COMPONENTS
import {SideBar} from './components/SideBar';
import {VersionLabel} from './components/VersionLabel';

//VIEWS
import Home from './views/Home/index';
import Calendar from './views/Calendar';
import Login from './views/Login';
import Meet from './views/Meet';

const App = (props) => {
  const [authed, setAuthed] = useState(false);

  //Check auth status
  useEffect(() => {
    const email = localStorage.getItem('email');
    const refreshToken = localStorage.getItem('refreshToken');
    const uid = localStorage.getItem('uid');

    if(email && refreshToken && uid) {
      setAuthed(true);
    }

    //Get firebase cloud msg token
    messaging.getToken().then(async function() {
      const token = await messaging.getToken();
      localStorage.setItem('pushToken', token);
    }).catch(function(err) {
      alert("Unable to get permission to notify.", err);
    });

    //Listen on received msg from Cloud
    messaging.onMessage(res => {
      showNotification(res.data.title, res.data.body); //Show notifications
    })
  }, [])

  if(!authed) return <Login onAuthed={() => setAuthed(true)}/>

  return (
    <div>
      <VersionLabel/>
      <SideBar/>
      <Main />
    </div>
  );
}

class Main extends Component {
  constructor(props) {
    super(props);
  }

  render() {
      return (
        <main>
          <Switch>
            <Route exact path='/' component={Home}/>
            <Route exact path='/calendar' component={Calendar}/>
            <Route exact path='/meet/:id' component={Meet}/>
          </Switch>
        </main>
      )
  }
}

export default App;