import { set } from 'lodash';
import React, {useState, useEffect} from 'react';
import {Database, randomId, DOMAIN, snapToArray, showNotification} from '../../Global/Variables';
import Axios from 'axios';

//Lấy ngày trong tháng
function getDaysInMonth(month, year, callback) {
    let months = parseInt(month) - 1;
    var date = new Date(year, months, 1);
    var days = [];
    while (date.getMonth() === months) {
      days.push(new Date(date));
      date.setDate(date.getDate() + 1);
    }
    return callback(days);
}

//Lấy ngày kế tiếp của tuần
function getNextWeekDate(date, endMonthDate, month, year, callback) {
    let dateNow = date;

    //Get nextweek day
    for(var i = 0; i < 7; i++) {
        dateNow = dateNow < endMonthDate ? dateNow + 1 : 1;
    }

    return callback(dateNow);
}

export function Controller(props) {
    const [calendar, setCalendar] = useState(null);
    const [month, setMonth] = useState(new Date().getMonth() + 1);
    const [year, setYear] = useState(new Date().getFullYear());
    const [createNewVisible, setCreateNewVisible] = useState(false);
    const [events, setEvents] = useState(null);
    const [editData, setEditData] = useState(null);
    const [calendarView, setCalendarView] = useState(2);
    const dayInWeek = ['CN', 'Th 2', 'Th 3', 'Th 4', 'Th 5','Th 6', 'Th 7'];
    const [dayViewData, setDayViewData] = useState(null);
    const [weekViewData, setWeekViewData] = useState(null);
    const [yearViewData, setYearViewData] = useState(null);
    //Day & Date Week
    const [dateWeekNow, setDateWeekNow] = useState(new Date().getDate());
    const [dayWeekNow, setDayWeekNow] = useState(new Date().getDay());

    useEffect(() => {
        //Get events
        Database.ref(`events`).on('value', (snapshot) => {
            let data = snapshot || {};
            data = snapToArray(data);
            data = data.sort((eventA, eventB) => {
                let fromDateA = new Date(eventA.fromDate);
                let fromDateB = new Date(eventB.fromDate);
                let toDateA = new Date(eventA.toDate);
                let toDateB = new Date(eventB.toDate);

                //if(fromDateA < fromDateB) return 1;
                if(fromDateA < fromDateB || toDateA > toDateB) return -1;
                if(fromDateA > fromDateB ||toDateA < toDateB) return 1;

                return 0;
            })

            setEvents(data);

            generateYearCalendar(new Date().getFullYear(),'today', data);
        })
    }, [])

    //Xây dựng lịch ngày
    const generateDayCalendar = (status, events) => {
        let endMonthDay = 0;
        let dayData = dayViewData || {
            day: new Date().getDay(),
            date: new Date().getDate(),
            year: new Date().getFullYear(),
            month: new Date().getMonth() + 1,
            events: events
        };
        let monthNow = month;
        let yearNow = year;

        //Lấy các ngày trong tháng
        getDaysInMonth(month, year, async (res) => {
            endMonthDay = res[res.length - 1].getDate(); //Lấy ngày của tháng cuối cùng

            //Xử lý lùi / tiến ngày trong chế độ xem ngày
            if(status === 'next') {//Next
                if(dayViewData.date < endMonthDay) { //Chưa cuối tháng
                    dayData.date += 1;
                    dayData.day = dayInWeek[res[dayData.date - 1].getDay()];
                }else if(dayViewData.date >= endMonthDay){ // Đang ở cuối tháng
                    dayData.date = 1;
    
                    monthNow = monthNow < 12 ? (monthNow + 1) : 1;
                    yearNow = monthNow > 1 ? yearNow : (yearNow + 1);
    
                    dayData.day = dayInWeek[new Date(yearNow, monthNow - 1, 1).getDay()];
                }
            }else if(status === 'prev') { //Prev
                if(dayViewData.date > 1) { //Không phải đầu tháng
                    dayData.date -= 1;
                    dayData.day = dayInWeek[res[dayData.date - 1].getDay()];
                }else if(dayViewData.date <= 1){ // Đang ở đầu tháng\
                    monthNow = monthNow > 1 ? (monthNow - 1) : 12;
                    yearNow = monthNow < 12 ? yearNow : (yearNow - 1);

                    getDaysInMonth(monthNow, yearNow, prevRes => {
                        dayData.date = prevRes[prevRes.length - 1].getDate();
                        dayData.day = dayInWeek[new Date(yearNow, monthNow - 1, dayData.date).getDay()];
                    })
                }
            }else{//Today
                monthNow = new Date().getMonth() + 1;
                monthNow = monthNow < 12 ? monthNow : 1;
                yearNow = new Date().getFullYear();
                yearNow = monthNow < 12 ? yearNow : yearNow + 1;

                dayData.date = new Date().getDate();
                dayData.day = dayInWeek[new Date().getDay()];
            }

            dayData.year = yearNow;
            dayData.month = monthNow;
                    
            setMonth(monthNow);
            setYear(yearNow);
            setDayViewData({...dayData});
        })
    }

    //Xây dựng lịch tuần
    const generateWeekCalendar = (status, events) => {
        let weekData = {
            data: [],
            month: null,
            year: null
        };
        let monthNow = month;
        let yearNow = year;
        let dayNow = dayWeekNow;
        let dateNow = dateWeekNow;
        let weekDays = [];
        let endMonthDay = 0;
        let prevEndMonthDay = 0;

        getDaysInMonth(monthNow || new Date().getMonth(), yearNow || new Date().getFullYear(), res => {
            endMonthDay = res[res.length - 1].getDate(); //Lấy ngày cuối cùng của tháng;
        
            if(status === 'next') { //Next
                getNextWeekDate(dateNow, endMonthDay, monthNow, yearNow, async nextDate => {
                    dateNow = (nextDate === 1 ? (endMonthDay - (dayNow - 1)) : nextDate);
                    dayNow = (nextDate === 1 ? 0 : dayNow);
                    
                    for(var i = 0; i < 7; i++) {
                        if(dayNow === i) {
                            weekDays.push({
                                day: dayInWeek[dayNow],
                                date: dateNow
                            })
                        }else{
                            if(dateNow - (dayNow - i) > endMonthDay) { //Đang ở cuối tháng
                                dateNow = 1;
                                dayNow = i;

                                weekDays.push({
                                    day: dayInWeek[i],
                                    date: 1
                                })
                            }else if(dateNow - (dayNow - i) < 1) {//đang ở ngày 1
                                getDaysInMonth(monthNow > 1 ? monthNow - 1 : 12, monthNow > 1 ? yearNow : yearNow - 1, prevRes => {
                                    prevEndMonthDay = prevRes[prevRes.length - 1].getDate();
                                    dateNow = prevEndMonthDay
    
                                    weekDays.push({
                                        day: dayInWeek[i],
                                        date: prevEndMonthDay
                                    })
                                })
                            } else{
                                weekDays.push({
                                    day: dayInWeek[i],
                                    date: dateNow - (dayNow - i)
                                })
                            }
                        }
                    }
                })
            }else if(status === 'prev') { //Prev
                let prevDateNow = dateNow;

                getDaysInMonth(monthNow > 1 ? monthNow - 1 : 12, monthNow > 1 ? yearNow : yearNow - 1, prevRes => {
                    prevEndMonthDay = prevRes[prevRes.length - 1].getDate();
                })

                //Get lấy ngày của tuần trước
                for(var i = 0; i < 7; i++) {
                    if(dateNow > 1) {
                        dateNow -= 1;
                    }else if(dateNow - (dayNow - i) <= 1) { //Đang ở đầu tháng
                        monthNow = monthNow > 1 ? monthNow - 1 : 12;
                        yearNow = monthNow > 1 ? yearNow : yearNow - 1;

                        getDaysInMonth(monthNow, yearNow, newRes => {
                            dateNow = newRes[newRes.length - 1].getDate();
                        })
                    }
                }

                if(dateNow === 1 && prevDateNow !== 1) {
                    dateNow = prevEndMonthDay - (dayNow - 1);
                    dayNow = 0;
                }

                for(var i = 0; i < 7; i++) {
                    if(dayNow === i) {
                        weekDays.push({
                            day: dayInWeek[dayNow],
                            date: dateNow
                        })
                    }else{
                        if(dateNow - (dayNow - i) > endMonthDay) { //Đang ở cuối tháng
                            dateNow = 1;
                            dayNow = i;

                            weekDays.push({
                                day: dayInWeek[i],
                                date: 1
                            })
                        }else if(dateNow - (dayNow - i) < 1) { //Đang ở đầu tháng
                            monthNow = monthNow > 1 ? monthNow - 1 : 12;
                            yearNow = monthNow > 1 ? yearNow : yearNow - 1;

                            getDaysInMonth(monthNow, yearNow, newRes => {
                                dateNow = newRes[newRes.length - 1].getDate();
                                dayNow = newRes[newRes.length - 1].getDay();
                            })
                        }else{
                            weekDays.push({
                                day: dayInWeek[i],
                                date: dateNow - (dayNow - i)
                            })
                        }
                    }
                }
            }else { //Today
                dayNow = new Date().getDay();
                dateNow = new Date().getDate();

                for(var i = 0; i < 7; i++) {
                    if(i === dayNow) {
                        weekDays.push({
                            day: dayInWeek[dayNow],
                            date: dateNow
                        })
                    }else{
                        //đang ở ngày 1
                        if(dateNow - (dayNow - i) < 1) {
                            getDaysInMonth(monthNow > 1 ? monthNow - 1 : 12, monthNow > 1 ? yearNow : yearNow - 1, prevRes => {
                                prevEndMonthDay = prevRes[prevRes.length - 1].getDate();
                                dateNow = prevEndMonthDay

                                weekDays.push({
                                    day: dayInWeek[i],
                                    date: prevEndMonthDay
                                })
                            })
                        }else if(dateNow - (dayNow - i) > endMonthDay) { //Đang ở cuối tháng
                            dateNow = 1;
                            dayNow = i;

                            weekDays.push({
                                day: dayInWeek[i],
                                date: 1
                            })
                        }else{
                            weekDays.push({
                                day: dayInWeek[i],
                                date: dateNow - (dayNow - i)
                            })
                        }
                    }
                }
            }
    
            weekData.data = weekDays;
            weekData.month = month || new Date().getMonth() + 1;
            weekData.year = year || new Date().getFullYear();
            weekData.events = events;
        })

        setWeekViewData(weekData);
        setDayWeekNow(dayNow);
        setDateWeekNow(dateNow);
    }

    //Xây dựng lịch tháng
    const generateCalendar = (month, year, events, status) => {
        let daysOfMonth = [];
        let daysInWeekOfMonth = [];

        getDaysInMonth(month, year, async (res) => {
            //Thêm các thứ trong tháng vào mảng
            for(var i = 0; i < res.length; i++) {
                daysInWeekOfMonth.push(dayInWeek[res[i].getDay()]);
            }

            //Thêm số 0 vào các ngày trống
            for(var i = 1; i < res[0].getDay(); i++) {
                daysOfMonth.push(0)
            } 
            
            //Nếu ngày đầu tiên rơi vào CN
            if(res[0].getDay() === 0) daysOfMonth = [0,0,0,0,0,0];
    
            //Thêm ngày vào mảng
            for(const date of res) {
                daysOfMonth.push(date.getDate());
            }
    
            //Thêm số 0 vào các ngày trống ở cuối
            for(var i = res[res.length - 1].getDay();i <= 6; i++) {
                daysOfMonth.push(0)
            }

            let data = []

            //Map event
            try {
                for(var i = 0; i < daysOfMonth.length; i++) {
                    let dateData = {
                        date: daysOfMonth[i],
                        events: []
                    }

                    //Map events
                    events.forEach(event => {
                        let fromDate = new Date(event.fromDate).getDate();
                        let toDate = new Date(event.toDate).getDate();
                       
                        if(fromDate <=  daysOfMonth[i] && toDate >= daysOfMonth[i]) {
                            dateData.events.push(event);
                        }
                    })

                    data.push(dateData);
                }
            }catch(e) {}

            //CalendarData 

            setCalendar(data);
            setMonth(month);
            setYear(year);

            try{
                props.onChange({date: daysOfMonth, month: month, year: year});
            }catch(e) {
                console.log(e);
            }
        })
    }

    //Xây dựng lịch năm
    const generateYearCalendar = (year, status, events) => {
        let dateData = {
            year: status === 'prev' ? year - 1 : status === 'next' ? year + 1 : new Date().getFullYear(),
            calendar: []
        }

        for(var i = 1; i <= 12; i++) {
            let daysOfMonth = [];
            
            getDaysInMonth(i, dateData.year, async (res) => {
                //Thêm số 0 vào các ngày trống
                for(var i = 1; i < res[0].getDay(); i++) {
                    daysOfMonth.push(0)
                }
            
                //Nếu ngày đầu tiên rơi vào CN
                if(res[0].getDay() === 0) daysOfMonth = [0,0,0,0,0,0];
        
                //Thêm ngày vào mảng
                for(const date of res) {
                    daysOfMonth.push(date.getDate());
                }
        
                //Thêm số 0 vào các ngày trống ở cuối
                for(var i = res[res.length - 1].getDay();i <= 6; i++) {
                    daysOfMonth.push(0)
                }

                dateData.calendar.push(daysOfMonth);
            })
        }

        setYear(dateData.year);

        if(status === 'today') {
            setMonth(new Date().getMonth() + 1);
            setYear(new Date().getFullYear());
            setDayViewData({
                date: new Date().getDate(),
                day: dayInWeek[new Date().getDay()],
                events: events,
                month: new Date().getMonth() + 1,
                year: new Date().getFullYear()
            })
            
            generateDayCalendar('today', events);
            generateCalendar(month, dateData.year, events);
            generateWeekCalendar('today',events);
        }

        setYearViewData(dateData);
    }

    //Generate calendar
    const calendarTransition = (status) => {
        let newMonth = month;
        let newYear = year;

        if(status === 'next') {
            if(calendarView === 2) {
                if(newMonth < 12) {
                    newMonth += 1;
                }else {
                    newMonth = 1;
                    newYear += 1;
                }
            }else if(calendarView === 3) {
                newYear += 1;
            }
        }else if(status === 'prev'){
            if(calendarView === 2) {
                if(newMonth > 1) {
                    newMonth -= 1;
                }else {
                    newMonth = 12;
                    newYear -= 1;
                }
            }else if(calendarView === 3) {
                newYear -= 1;
            }
        }else {
            newMonth = new Date().getMonth() + 1;
            newYear = new Date().getFullYear()
        }

        if(newYear !== year && calendarView !== 2) generateCalendar(newMonth, newYear, events, status);
        if(newYear !== year && calendarView !== 3) generateYearCalendar(year, status, events);

        //staatus = next / prev / today
        //Check view
        if(calendarView === 0) generateDayCalendar(status, events); //Day view
        if(calendarView === 1) generateWeekCalendar(status, events); //Week view
        if(calendarView === 2) generateCalendar(newMonth, newYear, events, status); //Month view
        if(calendarView === 3) generateYearCalendar(year, status, events); //Year view
    }

    //Transition for calendar controller
    const calendarControllerTransition = (status) => {
        let newMonth = month;
        let newYear = year;

        if(status === 'next') {
            if(newMonth < 12) {
                newMonth += 1;
            }else {
                newMonth = 1;
                newYear += 1;
            }
        }else if(status === 'prev'){
            if(newMonth > 1) {
                newMonth -= 1;
            }else {
                newMonth = 12;
                newYear -= 1;
            }
        }

        generateCalendar(newMonth, newYear, events, status);
        if(newYear !== year) generateYearCalendar(year, status, events);
    }

    return {
        calendar,
        month,
        setMonth,
        year,
        setYear,
        setCalendar,
        createNewVisible,
        setCreateNewVisible,
        events,
        setEditData,
        editData,
        calendarView,
        setCalendarView,
        dayViewData,
        weekViewData,
        yearViewData,
        calendarTransition,
        calendarControllerTransition
    }
}

export function CreateNewController(props) {
    const [meetId, setMeetId] = useState(null);
    const [fromDate, setFromDate] = useState(null);
    const [toDate, setToDate] = useState(null);
    const [allDay, setAllDay] = useState(false);
    const [editPerm, setEditPerm] = useState(false);
    const [invitePerm, setInvitePerm] = useState(false);
    const [viewListPerm, setViewListPerm] = useState(false);
    const [notifications, setNotifications] = useState([]);
    const [closeAlertVisible, setCloseAlertVisible] = useState(false);
    const [notificationLoop, setNotificationLoop] = useState(0);
    const [color, setColor] = useState({value: 0, label: 'red'});
    const dateFormat = 'DD thg MM, YYYY';

    useEffect(() => {
        //Update edit datepicker value
        if(props.editData) {
            let editData = props.editData;
            let fromDateStr = dateFormat;
            let toDateStr = dateFormat;
            let fromDate = new Date(editData.fromDate);
            let toDate = new Date(editData.toDate);

            fromDateStr = fromDateStr.replace(/DD/g, fromDate.getDate() <= 9 ? '0' + fromDate.getDate() : fromDate.getDate());
            fromDateStr = fromDateStr.replace(/MM/g, (fromDate.getMonth() + 1) <= 9 ? '0' + (fromDate.getMonth() + 1) : (fromDate.getMonth() + 1));
            fromDateStr = fromDateStr.replace(/YYYY/g, fromDate.getFullYear());

            toDateStr = toDateStr.replace(/DD/g, toDate.getDate() <= 9 ? '0' + toDate.getDate() : toDate.getDate());
            toDateStr = toDateStr.replace(/MM/g, (toDate.getMonth() + 1) <= 9 ? '0' + (toDate.getMonth() + 1) : (toDate.getMonth() + 1));
            toDateStr = toDateStr.replace(/YYYY/g, toDate.getFullYear());

            setFromDate(fromDateStr);
            setToDate(toDateStr);

            //Update notifications
            setNotifications(editData.notifications || []);

            //Update meet id
            setMeetId(editData.meetId);

            //Update perms
            setEditPerm(editData.guestPerms.edit);
            setInvitePerm(editData.guestPerms.invite);
            setViewListPerm(editData.guestPerms.viewList);

            //Update color
            setColor(editData.color);
        }
    }, [])

    //Create new event
    const handleSave = async (title, fromTime, toTime, allDay, location, description, guest) => {
        let eventId = randomId(24);
        let desc = description.current;

        let fromDateNew = fromDate.replace(/ thg /g, '-').replace(/, /g, '-');
        fromDateNew = fromDateNew.split('-').reverse();
        fromDateNew = new Date(fromDateNew[0], +fromDateNew[1] - 1, fromDateNew[2]).toISOString();

        let toDateNew = toDate.replace(/ thg /g, '-').replace(/, /g, '-');
        toDateNew = toDateNew.split('-').reverse();
        toDateNew = new Date(toDateNew[0], +toDateNew[1] - 1, toDateNew[2]).toISOString();

        desc = desc.innerHTML;
        desc = desc.replaceAll(/<b>/g, '2uf8b2uf9');
        desc = desc.replaceAll(/<\/b>/g, '2ua1b2uf9');
        desc = desc.replaceAll(/<i>/g, '2uf8i2uf9');
        desc = desc.replaceAll(/<\/i>/g, '2ua1i2uf9');
        desc = desc.replaceAll(/<u>/g, '2uf8u2uf9');
        desc = desc.replaceAll(/<\/u>/g, '2ua1u2uf9');

        //Create new notifications list waiting
        for(const item of notifications) {
            const timeType = ["phút", "giờ", "ngày", "tuần"];
            let dateNow = new Date();

            let toDateWithTime = new Date(toDateNew).setHours(toTime.split(":")[0]);
            toDateWithTime = new Date(toDateWithTime).setMinutes(toTime.split(":")[1]);

            let timeToLive = item.length;
            timeToLive = (1000 * 60 * (item.timeType === 0 ? timeToLive : item.timeType === 1 ? timeToLive * 60 : item.timeType === 2 ? timeToLive * 60 * 24 : timeToLive * 60 * 24 * 7))
            timeToLive = toDateWithTime - dateNow - timeToLive;

            let data = {
                to: localStorage.getItem('pushToken'),
                data : {
                    body: `Chỉ ${item.length} ${timeType[item.timeType]} nữa sẽ mở hội nghị truyền hình trên MokiMeeting`,
                    title: `MokiMeeting`
                },
                time_to_live: 6000
            }

            showNotification(`MokiMeeting`, `Chỉ ${item.length} ${timeType[item.timeType]} nữa sẽ mở hội nghị truyền hình trên MokiMeeting`)

            Axios.post('https://fcm.googleapis.com/fcm/send', data, {headers: {
                "Content-Type" : 'application/json',
                "Authorization" : "key=AAAAZ9KUY6A:APA91bFnt7D_eHykNNgJsZgFGhTKqRDm07xzTd5epcu1u_I4KCIprQjcqYKS6zOx1PqY--kYPRXfEeFDsDDdXqsHsXE2JYS5vs53IkbIe5bIyMfLQdPvLwAZxNTgipvF9PU39B0OBoJq"
            }}).catch(err => console.log(err))
        }


        //Create to firebase
        if(!props.editData) {
            Database.ref(`events/${eventId}`).set({
                id: eventId,
                title, 
                fromDate: fromDateNew, 
                fromTime, 
                toDate: toDateNew, 
                toTime, 
                notificationLoop, 
                notifications, 
                allDay, 
                location,
                color,
                meetId: meetId,
                description: desc,
                guest: guest,
                guestPerms: {
                    invite: invitePerm,
                    edit: editPerm,
                    viewList: viewListPerm
                }
            }).then(() => {
                props.onGoBack()
            })
        }else{
            //Save to firebase
            Database.ref(`events/${props.editData.id}`).update({
                title, 
                fromDate: fromDateNew, 
                fromTime, 
                toDate: toDateNew, 
                toTime, 
                notificationLoop, 
                notifications, 
                allDay, 
                location,
                color,
                meetId: meetId,
                description: desc,
                guest: guest,
                guestPerms: {
                    invite: invitePerm,
                    edit: editPerm,
                    viewList: viewListPerm
                }
            }).then(() => {
                props.onGoBack()
            })
        }
    }

    //Delete event
    const handleDelete = () => {
        Database.ref(`events/${props.editData.id}`).remove(() => props.onGoBack());
    }

    //Create new meet
    const handleCreateMeet = () => setMeetId(`${randomId(3)}-${randomId(4)}-${randomId(3)}`);

    //Redirect to meet
    const handleGoToMeet = () => window.open(`${DOMAIN}/#/meet/${meetId}`, '_blank');

    //Create new notifications
    const handleCreateNotification = () => {
        let notificationData = [...notifications];
        notificationData.push({
            timeType: 0,
            length: 10,
            time: null,
            type: 0
        })

        setNotifications(notificationData);
    }

    //Remove notifications
    const handleRemoveNotification = (index) => {
        let notificationData = [...notifications];
        notificationData.splice(index, 1);
        
        setNotifications(notificationData);
    }

    //Change notifications
    const handleChangeNotification = (index, type, value) => {
        let notificationData = [...notifications];
        notificationData[index][type] = value;

        setNotifications(notificationData);
    }

    return {
        handleSave,
        handleCreateMeet,
        meetId,
        handleDelete,
        setMeetId,
        fromDate,
        setFromDate,
        toDate,
        setToDate,
        allDay,
        setAllDay,
        notifications,
        setNotifications,
        handleCreateNotification,
        handleRemoveNotification,
        handleGoToMeet,
        setCloseAlertVisible,
        closeAlertVisible,
        notificationLoop,
        setNotificationLoop,
        handleChangeNotification,
        color,
        setColor,
        editPerm,
        setEditPerm,
        viewListPerm,
        setViewListPerm,
        invitePerm,
        setInvitePerm,
        dateFormat
    }
}