import React from 'react';
import './styles.scss';

export const Alert = (props) => {
    return (
        <div className={`alert-container modal ${props.visible ? 'visible' : ''}`}>
            <div className="alert">
                <div className="content">
                    <span className="title">{props.title}</span>
                </div>
                <div className="action-container">
                    <div className="action-btn" onClick={props.onClose}>Hủy</div>
                    <div className="action-btn recommend" onClick={props.onAccepted}>{props.acceptLabel}</div>
                </div>
            </div>
        </div>  
    )
}