import React, {useRef, useState} from 'react';
import {HeaderBar} from '../../components/HeaderBar';
import {SideBarContainer, SideBarHeader} from '../../components/SideBar';
import {IoLocationOutline, IoNotificationsOutline, IoCalendarOutline, IoCopyOutline, IoClose, IoMenuOutline} from 'react-icons/io5';
import {MdFormatBold, MdFormatUnderlined, MdFormatItalic, MdAttachment} from 'react-icons/md';
import {View} from '../../components/View';
import {CalendarController, EventWidget, FullCalendar} from '../../components/Calendar';
import {Checkbox} from '../../components/Checkbox';
import {Textbox , Textarea} from '../../components/Textbox';
import {Controller, CreateNewController} from './controller';
import {DOMAIN} from '../../Global/Variables';
import "./styles.scss";
import {ColorSelect, Select} from '../../components/Select';
import {LavenDatePicker} from '../../components/DatePicker';
import {Headbreak} from '../../components/Headbreak';
import {Alert} from '../../components/Alert';

const Calendar = (props) => {
    const {calendar, dayViewData, weekViewData, yearViewData, setCalendar,month, setMonth, year, setYear, setCalendarView, calendarView, events, editData, setEditData, createNewVisible, setCreateNewVisible, calendarTransition, calendarControllerTransition} = new Controller(props);

    if(!calendar || !yearViewData || !weekViewData || !dayViewData) return <div></div>

    if(createNewVisible) return <CreateNew onGoBack={() => {setCreateNewVisible(false); setEditData(null)}} editData={editData}/>

    return(
        <View flex={true} headerBar={true}>
            <HeaderBar calendarView={calendarView} onMinizeSecondSideBar={1} onChangeCalendarView={(e) => setCalendarView(+e.value)} onChangeSearch={1} calendarData={{month: month, year: year, date: dayViewData.date}} onNextCalendar={() => calendarTransition('next')} onTodayCalendar={() => calendarTransition('today')} onPrevCalendar={() => calendarTransition('prev')}/>
            <SideBarContainer>
                <div className='action-btn-create-calendar' onClick={() => setCreateNewVisible(true)}><svg width="36" height="36" viewBox="0 0 36 36"><path fill="#34A853" d="M16 16v14h4V20z"></path><path fill="#4285F4" d="M30 16H20l-4 4h14z"></path><path fill="#FBBC05" d="M6 16v4h10l4-4z"></path><path fill="#EA4335" d="M20 16V6h-4v14z"></path><path fill="none" d="M0 0h36v36H0z"></path></svg>Tạo</div>
                <CalendarController value={{date: calendar, month: month, year: year}} onNextCalendar={() => calendarControllerTransition('next')} onPrevCalendar={() => calendarControllerTransition('prev')}/>

                <hr/>
                <SideBarHeader>Lịch của tôi</SideBarHeader>
                <Checkbox label="Nhats Devil"/>
                <Checkbox label="Nhats Devil"/>
                <Checkbox label="Nhats Devil"/>

                <hr/>
                <SideBarHeader>Lịch hôm nay</SideBarHeader>
                {events.map(data => {
                    let fromDate = new Date(data.fromDate).getDate();
                    let toDate = new Date(data.toDate).getDate();
                    let today = new Date().getDate();

                    if(fromDate <= today && toDate >= today) return <EventWidget time={`${data.fromTime}`} name={`${data.title}`} avatar="" style={{marginTop: 16}}/>
                })}
            </SideBarContainer>
            <View overflow={true}>
                <FullCalendar view={calendarView} yearViewData={yearViewData} weekViewData={weekViewData} dayViewData={dayViewData} value={{date: calendar, month: month, year: year}} events={events} onEventClick={(e) => {setEditData(e); setCreateNewVisible(true)}}/>
            </View>
        </View>
    )
}

const CreateNew = (props) => {
    const {invitePerm, setInvitePerm, editPerm,dateFormat, setEditPerm, viewListPerm, setViewListPerm, setNotificationLoop, handleChangeNotification, color, setColor, notificationLoop, handleSave, handleCreateMeet, meetId,setCloseAlertVisible,closeAlertVisible, notifications, handleGoToMeet, handleRemoveNotification, handleCreateNotification, handleDelete, setMeetId, fromDate, setFromDate, toDate, setToDate, allDay, setAllDay} = new CreateNewController(props);
    const titleRef = useRef(0);
    const fromTimeRef = useRef(0);
    const toTimeRef = useRef(0);
    const locationRef = useRef(0);
    const descriptionRef = useRef(0);
    const guestRef = useRef(0);
    const editData = props.editData || {
        title: '',
        fromTime: '',
        toTime: '',
        fromDate: '',
        toDate: '',
        description: '',
        color: {
            value: 0,
            label: 'red'
        },
        allDay: false,
        guest: '',
        notificationLoop: 0,
        guestPerms: {
            invite: false,
            edit: false,
            viewList: false,
        }
    };

    return (
        <View flex={true} headerBar={false}>
            <Alert title="Loại bỏ các thay đổi chưa lưu?" acceptLabel={'Loại bỏ'} onAccepted={props.onGoBack} visible={closeAlertVisible} onClose={() => setCloseAlertVisible(false)}/>
            <div style={{display: 'flex', width: '100%'}}>
                <div style={{display: 'flex', width: '100%', boxSizing: 'border-box', padding: '24px 48px'}}>
                    <div style={{flex: 1, display: 'flex',position: 'relative', flexDirection: 'column', maxWidth: 641, marginRight: '24px', paddingLeft: 36, boxSizing: 'border-box'}}>
                        <IoClose style={{color: 'rgb(95, 99, 104)', width: 24, height: 24, position: 'absolute', top: 28, left: 2, cursor: 'pointer'}} onClick={() => setCloseAlertVisible(true)}/>
                        <Textbox label="Thêm tiêu đề" variant="underline" ref={titleRef} defaultValue={editData.title} style={{fontSize: 28}}/>

                        <div style={{display: 'flex', alignItems: 'center', marginTop: 8}}>
                            <LavenDatePicker format={dateFormat} onChange={(e) => setFromDate(e)} date={fromDate}/>
                            {!allDay ? <Textbox type="time" ref={fromTimeRef} style={{marginLeft: 10, flex: 1}} defaultValue={editData.fromTime}/> : null}
                            <span style={{marginTop: '16px', marginLeft: 8, marginRight: 8, color: '#3c4043', fontSize: 14}}>tới</span>
                            <LavenDatePicker format={dateFormat} onChange={(e) => setToDate(e)} date={toDate}/>
                            {!allDay ? <Textbox type="time" ref={toTimeRef} style={{marginLeft: 10, flex: 1}} defaultValue={editData.toTime}/> : null}
                        </div>
                        
                        <div style={{display: 'flex', alignItems: 'center', marginTop: 10}}>
                            <Checkbox label="Cả ngày" onChange={e => setAllDay(e)}/>
                            <Select options={[{
                                label: 'Không lặp lại',
                                value: 0
                            },{
                                label: 'Hằng tuần vào Chủ Nhật',
                                value: 1
                            },{
                                label: 'Hằng tháng vào ngày Chủ Nhật đầu tiên',
                                value: 2
                            },{
                                label: 'Hằng năm vào ngày 1 tháng 8',
                                value: 3
                            },{
                                label: 'Mọi ngày trong tuần ( từ thứ 2 đến thứ 6 )',
                                value: 4
                            }]} defaultValue={editData.notificationLoop} onChange={(e) => setNotificationLoop(e.value)} style={{marginLeft: 16}}/>
                        </div>

                        <Headbreak title="Chi tiết lịch họp"/>

                        <div>
                            <div style={{display: 'flex', alignItems: 'center', position: 'relative', marginLeft: -36}}>
                                <img src="https://ssl.gstatic.com/calendar/images/conferenceproviders/logo_meet_2020q4_192px.svg" width={20} height={20}/>
                                <button className="btn" style={{marginLeft: 19, fontWeight:500}} onClick={!meetId ? () => handleCreateMeet() : () => handleGoToMeet()}>{!meetId ? 'Thêm hội nghị truyền hình trên MokiMeeting' : 'Tham gia bằng MokiMeeting'}</button>
                                
                                {meetId ? 
                                    <div style={{display: 'flex', alignItems: 'center', position: 'absolute', right: 0}}>
                                        <IoCopyOutline style={{color: 'rgb(95, 99, 104)', width: 20, height: 20, cursor: 'pointer'}} onClick={() => {navigator.clipboard.writeText(`Đường liên kết đến MokiMeeting: ${DOMAIN}/#/meet/${meetId}`); alert('Đã copy')}}/>
                                        <IoClose style={{color: 'rgb(95, 99, 104)', width: 20, height: 20, marginLeft: 12, cursor: 'pointer'}} onClick={() => setMeetId(null)}/>
                                    </div> : null}
                            </div>
                            {meetId ? <span style={{marginTop: 8, marginLeft: 3, display: 'block'}}><a href={`${DOMAIN}/#/meet/${meetId}`} style={{color: '#70757a', fontSize: 12, textDecoration: 'none'}} target="_blank">{DOMAIN}/#/meet/{meetId} · Tối đa 100 người tham gia</a></span> : null}
                        </div>
                        
                        <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: 16, marginLeft: -36}}>
                            <IoLocationOutline style={{marginRight: 16, width: 24, height: 24, color: '#5f6368'}}/>
                            <Textbox type="text" label="Nhập địa điểm" ref={locationRef} defaultValue={editData.location} style={{marginTop: 0}}/>
                        </div>
                        
                        <div style={{display: 'flex', alignItems: 'center', marginLeft: -36}}>
                            
                            <div style={{display: 'flex',alignItems: `none`, justifyContent: 'center', marginTop: 8}}>
                                <IoNotificationsOutline style={{marginRight: 16, width: 24, height: 24, color: '#5f6368', flexShrink: 0, marginTop: 4}}/>
                                <div style={{display: 'flex', justifyContent: 'center', flexDirection: 'column'}}>
                                    {notifications.map((data, i) => {
                                        if(allDay) {
                                            return (
                                                <div style={{display: 'flex', marginBottom: 8, alignItems: 'center'}} key={`notification-item-${i}`}>
                                                    <Select options={[{
                                                            label: 'Thông báo',
                                                            value: 0
                                                        },{
                                                            label: 'Email',
                                                            value: 1
                                                        }]} defaultValue={data.type} onChange={(e) => handleChangeNotification(i, 'type', e.value)} style={{marginRight: 8}}/>
                                                    <span style={{marginRight: 8, fontSize: 14, color: '#3c4043'}}>trước</span>
                                                    <Textbox type="number" label="Thời gian" style={{width:240}} onChange={(e) => handleChangeNotification(i, 'length', e.target.value)} defaultValue={data.length || 10} style={{marginTop: 0}}/>
                                                    <Select options={[{
                                                        label: 'phút',
                                                        value: 0
                                                        },{
                                                            label: 'giờ',
                                                            value: 1
                                                        },{
                                                            label: 'ngày',
                                                            value: 2
                                                        },{
                                                            label: 'tuần',
                                                            value: 3
                                                    }]} defaultValue={data.timeType} onChange={(e) => handleChangeNotification(i, 'timeType', e.value)} style={{marginLeft: 8}}/>
                                                    <span style={{margin: '0 8px', fontSize: 14, color: '#3c4043'}}>lúc</span>
                                                    <Textbox type="time" style={{marginTop: 0}} onChange={(e) => handleChangeNotification(i, 'time', e.target.value)}/>
                                                    <IoClose style={{color: 'rgb(95, 99, 104)', width: 20, height: 20, marginLeft: 12, cursor: 'pointer', flexShrink: 0}} onClick={() => handleRemoveNotification(i)}/>
                                                </div>
                                            )
                                        }

                                        return (
                                            <div style={{display: 'flex', marginBottom: 8, alignItems: 'center'}} key={`notification-item-${i}`}>
                                                <Select options={[{
                                                        label: 'Thông báo',
                                                        value: 0
                                                    },{
                                                        label: 'Email',
                                                        value: 1
                                                    }]} defaultValue={data.type} onChange={(e) => handleChangeNotification(i, 'type', e.value)} style={{marginRight: 8}}/>
                                                <Textbox type="number" label="Thời gian" style={{width:240}} onChange={(e) => handleChangeNotification(i, 'length', e.target.value)} defaultValue={data.length || 10} style={{marginTop: 0}}/>
                                                <Select options={[{
                                                    label: 'phút',
                                                    value: 0
                                                    },{
                                                        label: 'giờ',
                                                        value: 1
                                                    },{
                                                        label: 'ngày',
                                                        value: 2
                                                    },{
                                                        label: 'tuần',
                                                        value: 3
                                                }]} defaultValue={data.timeType} onChange={(e) => handleChangeNotification(i, 'timeType', e.value)} style={{marginLeft: 8}}/>
                                                <IoClose style={{color: 'rgb(95, 99, 104)', width: 20, height: 20, marginLeft: 12, cursor: 'pointer', flexShrink: 0}} onClick={() => handleRemoveNotification(i)}/>
                                            </div>
                                        )
                                    })}
                                    <span style={{fontSize: 14, color: '#5f6368', fontWeight: 500, marginRight: 8, cursor: 'pointer', display: 'flex', height: 36, alignItems: 'center'}} onClick={handleCreateNotification}>Thêm thông báo</span>
                                </div>
                            </div>
                        </div>
                        
                        <div style={{display: 'flex', alignItems: 'center', marginTop: 8, marginLeft: -36}}>
                            <IoCalendarOutline style={{marginRight: 16, width: 24, height: 24, color: '#5f6368'}}/>
                            <span style={{fontSize: 14, color: '#5f6368', fontWeight: 500, marginRight: 8}}>Chọn màu hiển thị:</span>
                            <ColorSelect options={[
                                        {value: 0, label: 'red'},
                                        {value: 1, label: 'pink'},
                                        {value: 2, label: 'orange'},
                                        {value: 3, label: 'yellow'},
                                        {value: 4, label: 'green'},
                                        {value: 5, label: 'darkgreen'},
                                        {value: 6, label: 'blue'},
                                        {value: 7, label: 'purple'},
                                        {value: 8, label: 'lightpurple'},
                                        {value: 9, label: 'defaultpurple'},
                                        {value: 10, label: 'dark'},
                                        {value: 11, label: 'blue'}]} 
                                    
                                    onChange={(e) => setColor({value: e.value, label: e.label})} 
                                    defaultValue={editData.color.value}/>
                        </div>
                        
                        <div style={{display: 'flex', marginTop: 16, marginLeft: -36}}>
                            <IoMenuOutline style={{marginRight: 16, width: 24, height: 24, color: '#5f6368'}}/>
                            <div style={{width: '100%', borderRadius: 4}}>
                                <div style={{background: '#f1f3f4', borderTopLeftRadius: 4, borderTopRightRadius: 4, display: 'flex'}}>
                                    <div style={{width: 36, height: 36, display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                        <label for="file-dialog"><MdAttachment style={{color: 'rgba(0,0,0,0.54)',fontSize:20, margin: 8, marginBottom:0, cursor: 'pointer'}}/></label>
                                        <input type="file" style={{display: 'none'}} name="file-dialog" id="file-dialog"/>    
                                    </div>
                                    <div style={{userSelect: 'none', width: 36, height: 36, display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                        <MdFormatBold onClick={() => document.execCommand('bold')} style={{color: 'rgba(0,0,0,0.54)',fontSize:20, margin: 8, marginBottom:0, cursor: 'pointer'}}/>
                                    </div>
                                    <div style={{userSelect: 'none', width: 36, height: 36, display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                        <MdFormatItalic onClick={() => document.execCommand('italic')} style={{color: 'rgba(0,0,0,0.54)',fontSize:20, margin: 8, marginBottom:0, cursor: 'pointer'}}/>
                                    </div>
                                    <div style={{userSelect: 'none', width: 36, height: 36, display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                        <MdFormatUnderlined onClick={() => document.execCommand('underline')} style={{color: 'rgba(0,0,0,0.54)',fontSize:20, margin: 8, marginBottom:0, cursor: 'pointer'}}/>
                                    </div>
                                </div>
                                <div className="textbox textarea div" dangerouslySetInnerHTML={{__html: editData.description
                                        .replaceAll(/2uf8b2uf9/g, '<b>')
                                        .replaceAll(/2ua1b2uf9/g, '<\/b>')
                                        .replaceAll(/2uf8i2uf9/g, '<i>')
                                        .replaceAll(/2ua1i2uf9/g, '<\/i>')
                                        .replaceAll(/2uf8u2uf9/g, '<u>')
                                        .replaceAll(/2ua1u2uf9/g, '<\/u>')}} 
                                        ref={descriptionRef} 
                                        contentEditable="true" 
                                        style={{margin: 0, borderTopLeftRadius: 0, borderTopRightRadius: 0,justifyContent: 'unset', alignItems: 'unset', fontSize: 14}}>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div style={{flex: 1, display: 'flex', flexDirection: 'column', marginLeft: '24px', paddingTop: 171, maxWidth: 320, position: 'relative'}}>
                        <button className="btn" style={{display: 'inline-flex', width: 79, position: 'absolute', top: 20, left: 0, alignItems: 'center', justifyContent: 'center'}} onClick={() => handleSave(titleRef.current.value, fromTimeRef.current ? fromTimeRef.current.value : '', toTimeRef.current ? toTimeRef.current.value : '', allDay, locationRef.current.value, descriptionRef, guestRef.current.value)}>Lưu</button>
                        <Headbreak title="Thành phần họp"/>
                        
                        <Textbox label="Thêm khách" style={{width: 320}} ref={guestRef} defaultValue={editData.guest}/>
                        <span style={{fontSize: 14, marginBottom: 8, marginTop: 24}}>Quyền của khách</span>
                        
                        <Checkbox label="Sửa đổi sự kiện" onChange={(e) => setEditPerm(e)} checked={editData.guestPerms.edit}/>
                        <Checkbox label="Mời những người khác" onChange={(e) => setInvitePerm(e)} checked={editData.guestPerms.invite}/>
                        <Checkbox label="Xem danh sách mời" onChange={(e) => setViewListPerm(e)} checked={editData.guestPerms.viewList}/>
                    </div>
                </div>
            </div>
        </View>
    )
}

export default Calendar;