import React, {useState, useEffect} from 'react';

export default function Controller(props){
    const [checked, setChecked] = useState(props.checked || false);

    const onCheck = () => {
        setChecked(!checked);
        try{
            props.onChange(!checked);
        }catch(e) {
            console.log(e);
        }
    }

    return {
        checked,
        setChecked,
        onCheck
    }
}

