import React, {useRef, useEffect} from 'react';
import "./styles.scss";
import Arrow  from './arrow.svg';
import DisabledArrow from './disabledArrow.svg';
import {Controller} from './controller';
import {IoMdArrowDropdown} from 'react-icons/io';

export const Select = (props) => {
    const {optionVisible, showOption, setOptionVisible, select, selected} = new Controller(props);
    const componentRef = useRef(0);
    const options = props.options;
    const disabled = props.disabled;

    //Setup onclick event
    useEffect(() => {
        document.addEventListener('mousedown', clickOutside);
    }, [])

    //Click outside event
    const clickOutside = (e) => {
        try{
            if (componentRef && !componentRef.current.contains(e.target)) {
                setOptionVisible(false);
            }
        }catch(e) {
            console.log(e);
        }
    }

    return(
        <div className={`select ${optionVisible ? 'visible' : ''} ${disabled  ? 'disabled' : ''}`} ref={componentRef} style={props.style}>
            <div className="box" onClick={showOption}>
                <span className="label">{options[selected].label}</span>
                <div className="mark">
                    <IoMdArrowDropdown className="icon"/>
                </div>
            </div>
            <ul className={`option-container`}>
                {options.map((data, i) => {
                    return <li className="item" key={`option-val-${i}`} onClick={() => select(data, i)}>{data.label}</li>
                })}
            </ul>
        </div>
    )
}

