import React from 'react';
import {View} from '../../components/View';
import MeetComponent from '../../components/Jitsi';

const Meet = (props) => {
    return (
        <View style={{height: '100vh'}}>
            <MeetComponent userName={localStorage.getItem('email')} roomId={props.match.params.id}/>
        </View>
    )
}

export default Meet;