import React from 'react';
import "./styles.scss";

export const Textbox = React.forwardRef((props, ref) => (
        <div className={`textbox ${props.variant}`} style={props.style}>
            <input onClick={props.onClick} readOnly={props.readOnly} disabled={props.disabled} type={props.type} onChange={props.onChange} ref={ref} defaultValue={props.defaultValue} placeholder={props.label} value={props.value}/>
        </div>
    )
);

export  const Textarea = React.forwardRef((props, ref) => (
    <div className="textbox textarea" style={props.style}>
        <textarea type={props.type} onChange={props.onChange} ref={ref} defaultValue={props.defaultValue} placeholder={props.label} value={props.value}>{props.children}</textarea>
    </div>
))