import React from 'react';
import {IoCalendarOutline, IoSearch, IoChevronForward, IoChevronBack, IoTrashOutline} from 'react-icons/io5';
import {MdArrowBack} from 'react-icons/md';
import {AiOutlinePlus} from 'react-icons/ai';
import Button from '@material-ui/core/Button';
import "./styles.scss";
import {Select} from '../Select';
import Controller from './controller';

export const HeaderBar = (props) => {
    const {searchBarVisible, setSearchBarVisible} = new Controller(props);

    //Search bar
    if(searchBarVisible) {
        return (
            <div className="headerbar">
                <div className="left">
                    <div className="back-btn" onClick={() => setSearchBarVisible(false)}>
                        <MdArrowBack className="icon"/>
                    </div>
                    <h2 className="header">Tìm kiếm</h2>
                </div>
                
                <div className="center">
                    <div className="searchbar">
                        <IoSearch className="icon"/>
                        <input type="text" placeholder="Tìm kiếm" className="second"/>
                    </div>
                </div>

                <div className="right">

                </div>
            </div>
        )
    }

    return(
        <div className="headerbar">
            <div className="left">
                {props.onGoBack ? <div className="goback" onClick={props.onGoBack}><IoChevronBack className="icon"/><span>{props.goBackTitle}</span></div> : null}
                {props.onMinizeSecondSideBar ? <button className="btn second nolabel"><IoCalendarOutline className="icon"/></button> : null}
                <div className="calendar">
                    {props.onNextCalendar && props.onPrevCalendar && props.onTodayCalendar  ?
                    <div className="action">
                        <Button variant="outlined" className="btn outline" onClick={props.onTodayCalendar}>Hôm nay</Button>  
                        <div className="action-btn"><IoChevronBack className="icon" onClick={props.onPrevCalendar}/></div>
                        <div className="action-btn"><IoChevronForward className="icon" onClick={props.onNextCalendar}/></div>
                    </div> : null}
                    {props.calendarData ? <span className="date">{props.calendarView === 1 || props.calendarView === 2 ? `Tháng ${props.calendarData.month},` : props.calendarView === 0 ? `${props.calendarData.date} tháng ${props.calendarData.month},` : ''} {props.calendarData.year}</span> : null}
                </div>
            </div>
            <div className="center">
            </div>
            <div className="right">
                {props.onChangeCalendarView ? 
                <Select options={[{
                    value: 0,
                    label: 'Ngày'
                },{
                    value: 1,
                    label: 'Tuần'
                },{
                    value: 2,
                    label: 'Tháng'
                },{
                    value: 3,
                    label: 'Năm'
                }]} onChange={props.onChangeCalendarView}/> : null }
                <div className="search-btn" onClick={() => setSearchBarVisible(true)}>
                    <IoSearch className="icon"/>
                </div>
                {props.onDelete ? <button className="btn third" onClick={props.onDelete}><IoTrashOutline className="icon"/> Xóa</button> : null}
                {props.onSave ? <button className="btn" onClick={props.onSave}><AiOutlinePlus className="icon"/> Lưu</button> : null}
            </div>
        </div>
    )
}