import React, {useState, useEffect} from 'react';
import {Database, findWithAttr} from '../../Global/Variables';

//Lấy ngày trong tháng
function getDaysInMonth(month, year, callback) {
    let months = parseInt(month) - 1;
    var date = new Date(year, months, 1);
    var days = [];
    while (date.getMonth() === months) {
      days.push(new Date(date));
      date.setDate(date.getDate() + 1);
    }
    return callback(days);
  }

export default function Controller(props) {
    const [calendar, setCalendar] = useState(null);
    const [month, setMonth] = useState(0);
    const [year, setYear] = useState(0);
    const [focusDate, setFocusDate] = useState(-1);
    const [yearEventPopupVisible, setYearEventPopupVisible] = useState(false);
    const [mouseClickPosX, setMouseClickPosX] = useState(0);
    const [mouseClickPosY, setMouseClickPosY] = useState(0);
    const [focusData, setFocusData] = useState(null);
    const daysInWeek = ['TH 2', 'TH 3', 'TH 4', 'TH 5', 'TH 6', 'TH 7', 'CN'];

    useEffect(() => {
        generateCalendar(new Date().getMonth() + 1,new Date().getFullYear())
    }, [])

    //Get mouse click position
    useEffect(() => {
        window.onclick = (e) => {
            e.preventDefault();
            setMouseClickPosX(e.pageX - 622);
            setMouseClickPosY(e.pageY - 70);
        }
    }, [])

    //Xây dựng lịch tháng
    const generateCalendar = (month, year) => {
        let daysOfMonth = []
        getDaysInMonth(month, year, async (res) => {
            //Thêm số 0 vào các ngày trống
            for(var i = 1; i < res[0].getDay(); i++) {
                daysOfMonth.push(0)
            }
    
            //Thêm ngày vào mảng
            for(const date of res) {
                daysOfMonth.push(date.getDate());
            }
    
            //Thêm số 0 vào các ngày trống ở cuối
            for(var i = res[res.length - 1].getDay();i <= 6; i++) {
                daysOfMonth.push(0)
            }

            let data = []

            //Map event
            try {
                for(const date in daysOfMonth) {
                    let dateData = {
                        date: date,
                        events: []
                    }
                    
                    //Map events
                    props.events.forEach(event => {
                        let fromDate = new Date(event[1].fromDate).getDate();
                        let toDate = new Date(event[1].toDate).getDate();
                       
                        if(fromDate <= date && toDate >= date) {
                            dateData.events.push(event[1]);
                        }
                    })

                    data.push(dateData);
                }
            }catch(e) {}

            //CalendarData 

            setCalendar(data);
            setMonth(month);
            setYear(year);

            try{
                props.onChange({date: daysOfMonth, month: month, year: year});
            }catch(e) {
                console.log(e);
            }
        })
    }

    //Generate next
    const generateNextCalendar = () => {
        if(props.value) {
            props.onNextCalendar();
        }else{
            generateCalendar(month < 12 ? month + 1 : 1, month < 12 ? year : year + 1);
        }
    }

    //Generate Back
    const generatePrevCalendar = () => {
        if(props.value) {
            props.onPrevCalendar();
        }else{
            generateCalendar(month > 1 ? month - 1 : 12, month > 1 ? year : year - 1);
        }
    }

    //Focus to date
    const handleFocus = (day, date, month, year) => {
        let focusData = {
            day: daysInWeek[day],
            date: date,
            month: month, 
            year: year
        }

        setFocusData(focusData);
        setYearEventPopupVisible(true);
    }

    return {
        generateCalendar,
        calendar,
        month,
        year,
        setFocusDate,
        focusDate,
        generateNextCalendar,
        generatePrevCalendar,
        handleFocus,
        yearEventPopupVisible,
        setYearEventPopupVisible,
        mouseClickPosX,
        mouseClickPosY,
        focusData
    }
}