import React from 'react';
import Controller from './controller'; 
import Mark from './checkmark.svg';
import "./styles.scss";

export const Checkbox = (props) => {
    const {checked, onCheck} = new Controller(props);

    return(
        <div className={`checkbox ${checked ? 'checked' : ''} ${props.disabled ? 'disabled' : ''}`} onClick={onCheck}>
            <div className={`box ${checked ? 'checked' : ''} ${props.disabled ? 'disabled' : ''}`}>
                <span className="checkmark">
                    <img src={Mark}/>
                </span>
            </div>
            <span className="label">
                {props.label}
            </span>
        </div>
    )
}