import React, {useRef} from 'react';
import {View} from '../../components/View';
import {Textbox} from '../../components/Textbox';
import "./styles.scss";
import {LoginController} from './controller';

const Login = (props) => {
    const {handleLogin} = new LoginController(props);
    const email = useRef(0);
    const password = useRef(0);

    return(
        <View className="view">
            <div className="form">
                <h1>LOGIN</h1>
                <Textbox label="Email" type="email" ref={email}/>
                <Textbox label="Password" type="password" ref={password}/>
                <button className="btn" onClick={() => handleLogin(email.current.value, password.current.value)}>Đăng nhập</button>
            </div>
        </View>
    )
}

export default Login;